<template>
  <div class="content-wrapper" ref="nodes">
    <div class="title">告白墙</div>
    <div class="note">
      <note-item v-for="(item,i) in notes" :imgSrc="item.src" :elStyle="item.style"
                 v-bind:key="i"></note-item>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .content-wrapper {
    box-sizing: border-box;
    /*border: 40px solid rgba(0, 0, 0, .3);*/
    height: calc(100vh - 100px);
  }

  .note {
    height: calc(100vh - 160px);
    position: relative;
    overflow: hidden;
  }

  .title {
    height: 60px;
    background: rgba(0, 0, 0, 0.4);
    font-size: 34px;
    font-weight: bold;
    text-align: center;
    line-height: 60px;
    color: #fffacd;
  }
</style>
<script>
  import {addResizeListener, removeResizeListener} from 'element-ui/lib/utils/resize-event'
  import noteItem from './note-item'

  export default {
    components: {
      noteItem
    },
    data() {
      return {
        width: 0,
        height: 0,
        notes: []
      }
    },
    methods: {
      getNodes() {
        for (let i = 0; i < 81; i++) {
          // let deg = Math.floor(20 * Math.random()) + 'deg'
          let flag = Math.floor(40 * Math.random()) % 2 ? '-' : '+'
          let top = this.height * Math.random() >= this.height - 150 ? this.height - 150 : this.height * Math.random()
          this.notes.push({
            src: `/img/${i}.jpg`,
            style: {
              top: top + 'px',
              left: this.width * Math.random() + 'px',
              transform: 'rotate(' + flag + 40 * Math.random() + 'deg)'
            }
          })
          console.log(this.notes, 'notes')
        }
      },
      fitEvent() {
        this.width = this.$refs.nodes.getBoundingClientRect().width - 160
        this.height = this.$refs.nodes.getBoundingClientRect().height - 200

        console.log(this.width, this.height, this.$refs.nodes.getBoundingClientRect(), 'wh')
        this.getNodes()
      }
    },
    mounted() {
      this.proxyFitEvent = this.fitEvent.bind(this)
      addResizeListener(this.$refs.nodes, this.proxyFitEvent)
    },
    beforeDestroy() {
      removeResizeListener(this.$refs.nodes, this.proxyFitEvent)
    }
  }
</script>
