<template>
  <div ref="loveHeader" class="footer">
    <div :style="{width:boxW,margin:'auto',overflow:'hidden'}">
      <div class="box-wrapper"
           v-for="(item,index) in imgList"
           :key="index"
           v-if="index<count-1">
        <el-popover
          placement="top"
          trigger="hover">
          <img :src="item.src" alt="" class="popover-img">
          <div class="box" slot="reference">
            <img :src="item.src" alt="" class="img">
          </div>
        </el-popover>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .footer {
    background: rgba(0, 0, 0, 0.2);
  }

  .box-wrapper {
    padding: 8px;
    float: left;
  }

  .img {
    width: 100px;
    height: 80px;
    display: block;
  }

  .box {
    text-align: center;
  }
  .popover-img {
    width: 480px;

  }
</style>
<script>
  import {addResizeListener, removeResizeListener} from 'element-ui/lib/utils/resize-event'

  export default {
    components: {},
    data() {
      return {
        imgList: [],
        width: 0,
        count: 0,
        boxW: ''
      }
    },
    computed: {},
    methods: {
      getImgs() {
        for (let i = 0; i <= 30; i++) {
          this.imgList.push({
            src: `/img/${i}.jpg`,
            index: 1
          })
        }
      },
      fitEvent() {
        this.width = this.$refs.loveHeader.getBoundingClientRect().width
        this.count = this.width / 116
        this.boxW = Math.floor(this.count) * 116 + 'px'
        console.log(this.width, this.count, this.boxW)
      }
    },
    created() {
      this.getImgs()
    },
    mounted() {
      this.proxyFitEvent = this.fitEvent.bind(this)
      addResizeListener(this.$refs.loveHeader, this.proxyFitEvent)
    },
    beforeDestroy() {
      removeResizeListener(this.$refs.loveHeader, this.proxyFitEvent)
    }
  }
</script>
